.x-mark {
    width: 40px;
    height: 40px;
    position: relative;
    border-radius: 6px;
    cursor: pointer;
}

.x-mark:before, .x-mark:after {
    content: '';
    position: absolute;
    width: 36px;
    height: 4px;
    background-color: #8b8b8b;
}

.x-mark:before {
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    transform: rotate(45deg);
    left: -18px;
}

.x-mark:after {
    -webkit-transform: rotate(-45deg);
    -moz-transform: rotate(-45deg);
    transform: rotate(-45deg);
    right: -18px;
}

.modal-header {
    text-align: right;
    border-bottom: none;
}

.modal-body {
    margin: 0 auto;
    width: 90%;
    padding: 0 15px;
}

.modal-footer {
    border-top: none;
    text-align: center;
}

.modal-footer button {
    display: inline-block;
    width: 80%;
    padding: 6px 12px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.42857143;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-image: none;
    border: 1px solid #000;
    border-radius: 4px;
    background-color: #F9ED9E;
}

.inquiry-wrap h1 {
    font-size: 20px;
    font-weight: 700;
}

.inquiry-wrap * {
    box-sizing: border-box;
}

.inquiry-wrap hr {
    border-top-color: #000;
}

#inquiry_submit img {
    width: 100%;
}

.inquiry-group {
    margin-bottom: 15px;
}

.inquiry-label {
    width: 20%;
    padding: 3px 15px 0 15px;
    float: left;
    text-align: right;
    display: inline-block;
    max-width: 100%;
    font-weight: 700;
}

.inquiry-input-wrap {
    float: left;
    width: 80%;
}

.inquiry-input {
    display: block;
    width: 100%;
    height: 28px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
}

.inquiry-textarea {
    display: block;
    width: 100%;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
}

.inquiry-radio-wrap {
    width: 50%;
    float: left;
}

